import React, { useState,useEffect } from 'react'
import '../Button.css'
import Footer from '../Footer'
import '../Forms.css'

const Requests = () => {
        const [location,setLocation] = useState("")
        const [Bedrooms,setBedroom] = useState("")
        const [budget, setBudget] = useState("")
        const [requests,setRequest] = useState([])
        const [type,setType] = useState("")
        const [fname,setFname] = useState("")
        const [email,setEmail] = useState("")
        const [phone,setPhone] = useState("")
        const [serviced,isServiced] = useState(true)
        const [furnished,isFurnished] = useState(true)
        const [comment,setComment] = useState("")
        useEffect(() => {
            const getRequests = async () =>{
                const requestsFromServer = await fetchRequests()
    setRequest(requestsFromServer)
            }
            getRequests()
            
        }, [])
        const onSubmit = (e)=> {
            e.preventDefault()
            const request = {location:location,bed:Bedrooms,budget:budget,type:type,serviced:serviced,furnished:furnished,name:fname,email:email,comment:comment,}
        
            addRequest(request)
            alert("Request successfully made")
            setLocation("")
            setBedroom("")
            setBudget("")
            setType("")
            setComment("")
            
        }

        const fetchRequests = async () => {
            const res = await fetch('http://localhost:4000/requests')
            const data  = await res.json()

            return data
        }

        const addRequest = async (request) =>{
            
            const res = await fetch('http://localhost:4000/requests',{
            method: 'POST',
            headers: {
            'Content-type':'application/json',

            },
            body : JSON.stringify(request)
        })
      const data = await res.json()
      setRequest([...requests, data])

     
      const id = Math.floor(Math.random() * 1000) + 1
      const newRequest = {id, ...request}
      console.log(newRequest);
      setRequest([...requests,newRequest])

        }

    return (
        <div>
            <h1 className='requests'>Requests</h1>
            <div className='s-container p-3'>
            <form onSubmit={onSubmit} >
            <div className='form-row'>
                <div className='form-group form-edit m-2 col-sm-10'>  
                <label>Location</label> 
            <input className='form-control  ' type='text' value={location} onChange={(e) => setLocation(e.target.value)}  placeholder='Enter A State, Locality or Area' />
                </div>
                </div>
                <div className='form-row'>
                <div className='form-group form-edit m-2  col-sm-2'>  
                Rooms
               <select className='form-control' value={Bedrooms} onChange={(e) => setBedroom(e.target.value)}>
                        <option disabled >Select Number of Rooms </option>
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4</option>
                        <option>5</option>
                        <option>6</option>
                        <option>7</option>
                        <option>8</option>
                    </select>
                    </div>
                    <div className='form-group form-edit m-2 col-sm-2 '>
                      <label>  Lease Type</label>
                    <select className='form-control '  value={type} onChange={(e) => setType(e.target.value)} >
                        <option>Rent</option>
                        <option>Sale</option>
                        <option>Lease</option>
                        <option>Joint Venture</option>
                    </select>  
                    </div>
                    <div className='form-group form-edit m-2 col-sm-2 mb-2'>
                        <label>Your Budget</label>
                    <input className='form-control' type='text' value={budget} onChange={(e) => setBudget(e.target.value)}  placeholder='Enter your budget in naira' />
                </div>
                
                <div className='form-group form-edit m-2 checkbox col-sm-1'>
                <label>Serviced</label>
                <input className=' ' type="checkbox" value={serviced} 
                onChange={(e) => isServiced(e.currentTarget.checked)}/>
                </div>
                <div className='form-group form-edit m-2 checkbox   col-sm-2'>
                <label >Furnished</label>
                <input className='' type="checkbox" value={furnished} 
                onChange={(e) => isFurnished(e.currentTarget.checked)}/>
                </div>
                </div>
                <div className='form-row m-2'>
                <div className='form-group form-edit m-1  col-sm-3'>
                    <label>Full Name</label>
                    <input className='form-control  col-bg-6' type='text' value={fname} onChange={(e) => setFname(e.target.value)}  placeholder='Enter your name here' />
            </div>
                <div className='form-group form-edit m-1 col-sm-3'>
                    <label>Email</label>
                    <input className='form-control  col-bg-6' type='email' value={email} onChange={(e) => setEmail(e.target.value)}  placeholder='Enter your Email' />
       
                </div>
                <div className='form-group form-edit m-1 col-sm-2'>
                    <label>Phone</label>
                    <input className='form-control  col-bg-6' type='phone' value={phone} onChange={(e) => setPhone(e.target.value)}  placeholder='Enter your phone number' />
                       </div>
                </div>
                <div className='form-row'>
                <div className="form-group form-edit m-2 col-sm-12">
              <label for="comments">Comments</label>
                <textarea className="form-control" rows="10" name="comments" value={comment} onChange={(e) => setComment(e.target.value)} required ></textarea>
                
              </div>
                <div className='form-group'>
                <input className='d-flex btn btn--primary btn--medium m-2' type='submit' name='Submit'  value='Submit Request'/>
                </div>
                </div>
            </form>
            </div>
            <Footer></Footer>
        </div>
    )
}

export default Requests
