import React, { useState, useEffect } from 'react'
import { Redirect} from 'react-router-dom'

import Login from '../Login'
const Register = () => {
    const [fname,setFname] = useState("")
    const [email,setEmail] = useState("")
    const [password,setPassword] = useState("")
    const [cpassword,setCpassword] = useState("")
    const [userType,setUserType] = useState("")
    const [phone,setPhone] = useState("")
    const [users,setUsers]  = useState([])
    const [login,setLoginform] = useState("")
    const [lEmail,setLEmail] = useState("")
    const [loggedIN,setLoggedin] = useState(false)
    const [lPassword,setLPassword] = useState("")
    useEffect(() =>{
        const getTasks = async () => {
          const usersFromServer = await fetchUsers()
          setUsers(usersFromServer)
      
        }
        
        getTasks()
      },[])

      const fetchUsers = async () =>{
          const res = await fetch('https://hidden-forest-99115.herokuapp.com')

          const data = await res.json()
          return data
      }
    const onSubmit = (e) =>{
        e.preventDefault()
        // setUsers(fname,email,password,userType,phone)
        
        addUser({fname,email,password,userType,phone});
        alert('User successfully created. You can now login')
        
    }
    const signin = (e) => {
        e.preventDefault()

        verifyUser(lEmail,lPassword)
        setLEmail("")
        setLPassword("")
    }

    const verifyUser = async( email,password) => {
        const usersFromServer = await fetchUsers()
        console.log('in verify')
        if(usersFromServer.find((user) => user.email === email && user.password === password)){
            setLoggedin(true)
            
        }else{
            alert('Wrong sign in information')
        }

    }
  
    
    const addUser = async (user) =>{
        console.log(user)
        const res = await fetch('http://localhost:5000/users', {
            method: 'POST',
            headers: {
                'Content-type':'application/json'
            },
            body:JSON.stringify(user)
        })

        const data = await res.json()
        setUsers([...users,data])
        const id  = Math.floor(Math.random()*10988 )-1

        const createdUser = {id, ...user} 
        // console.log(createdUser)
        setUsers([...users,createdUser])


    }
//     function usee(){
//    // setUserType('data')
//     return true
//     }
    return (
       
        <div className=''>
            {loggedIN?<Redirect to={{ pathname: '/Profile' , state: { email: {email} } } } />: ""}
            <h1 className='sign-up m-2 '>Hello There</h1>
            
            <div className='form-reg m-10' >
            <h3 className=''>Create Your account</h3>
            <div className='upper-bar'>
<button className={`btn--norad  ${userType==='agent'?'btn--outline':'btn--primary'}`} onClick={(e) => setUserType('agent')} > Agent </button>
<button className={`btn--norad  ${userType==='developer'?'btn--outline':'btn--primary'}`} onClick={(e) => setUserType('developer')}> Developer </button>
<button className={`btn--norad  ${userType==='landlord'?'btn--outline':'btn--primary'}`} onClick={(e) => setUserType('landlord')}> Landlord </button>
<button className={`btn--norad  ${userType==='user'?'btn--outline':'btn--primary'}`} onClick={(e) => setUserType('user')}> User </button>
            </div>
            <div className='form-inline'>
                <form onSubmit={onSubmit}>
                    <div className='form-row'>
                    <div className='form-group form-edit col-sm-6'>
                    <label>Full Name</label>
                    <input className='form-control' value={fname} onChange={(e) => setFname(e.target.value)} />
                    </div>
                    </div>
                    <div className='form-row'>
                    <div className='form-group col-sm-6'>
                    <label>Email Address</label>
                    <input className='form-control' type="email" autoComplete='email' value={email} onChange={(e) => setEmail(e.target.value)}/>
                    </div>
                    </div>
                    <div className='form-row'>
                    <div className='form-group col-sm-6'>
                    <label>Password</label>
                    <input className='form-control' type="password" autoComplete='password' value={password}  onChange={(e) => setPassword(e.target.value)}/>
                    </div>
                    </div>
                    <div className='form-row'>
                    <div className='form-group col-sm-6'>
                    <label>Confirm password</label>
                    <input className='form-control' type="password" autoComplete='password' value={cpassword} onChange={(e) => setCpassword(e.target.value)}/>
                    </div>
                    </div>
                    <div className='form-row'>
                    <div className='form-group col-sm-6'>
                    <label>Phone Number</label>
                    <input className='form-control' type="phone" autoComplete='phone' value={phone} onChange={(e) => setPhone(e.target.value)}/>
                    </div>
                    <div className='form-group d-flex  mt-2'>
                        <input className='btn--norad col-sm-6 btn--primary ' type='submit' value='register'  />
                    </div>
                    <div className='form-group    mt-2'>
                        
                    </div>
                    </div>
                    
                    


                </form>
                <button className='btn btn--medium btn--outline' onClick={(e) => setLoginform(true)}  >Sign In  </button>
                {login?(
                    <form onSubmit={signin}>
                        <label>Email Address</label>
                        <input value={lEmail} type="email" onChange={(e) => setLEmail(e.target.value)} placeholder='Email'  /> 
                        <label>password</label>
                        <input value={lPassword} type="password" onChange={(e) => setLPassword(e.target.value)} placeholder='Enter Password'  /> 
                        <button >Log in</button>
                    </form>



                ):<Login />}
                
                </div>
            </div>

        </div>
    )
}

export default Register
